    width: 100%;
    height: 100%;
}

*{
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    outline: none;
}

/*   */


.matrixFrame{
    color: hsla(0,0%,100%,.34);
    background-color: #0e1a1b;
}


.row {
    display: table-row;
    vertical-align: middle;
    text-align: center;
    font-weight: 600;
    font-size: 10pt;

    
    
}

.numberOfColumn{
    height: 24px;
