    display: table-cell;
}

.numberOfRow{
    height: 72px;
    min-width: 24px;
    display: table-cell;
}

.vertex{
    display: table-cell;
    height: 24px;
    width: 24px;
}

.Square{
    display: table-cell;
}

.chemicalElement:hover{
    background-color:  #0e1a1b ;
    cursor: pointer;
}


